import { ShapeAttrs } from '@antv/g-base';
import { Options, AttributeOption, AdjustOption, LabelOption } from '../dependents';
import { LooseMap } from './types';

export interface ITitle {
  visible: boolean;
  text: string;
  style?: {};
  alignWithAxis?: boolean;
  alignTo?: 'left' | 'right' | 'middle';
}

export interface IDescription {
  visible: boolean;
  text: string;
  style?: {};
  alignWithAxis?: boolean;
  alignTo?: 'left' | 'right' | 'middle';
}

type IEvents = LooseMap<string>;

export type Formatter = (text: string, item: any, idx: number) => string;

/**
 *  Shape 
 */
export interface IStyleConfig extends ShapeAttrs {}

export interface IBaseAxis {
  /** true */
  visible?: boolean;
  /** scale */
  type?: 'linear' | 'time' | 'cat' | 'dateTime' | 'category' | 'log' | 'pow' | 'timeCat';
  /** scale  nice */
  nice?: boolean;
  /** scale  tickMethod */
  tickMethod?: string | ((cfg: any) => number[]);
  /**  */
  line?: {
    visible?: boolean;
    style?: IStyleConfig;
  };
  grid?: {
    /**  */
    visible?: boolean;
    line?: {
      style?: IStyleConfig | ((text: string, idx: number, count: number) => IStyleConfig);
      type?: 'line' | 'circle';
    };
    /**  */
    alternateColor?: string | string[];
  };
  autoEllipsisLabel?: boolean;
  autoRotateTitle?: boolean;
  label?: {
    visible?: boolean;
    formatter?: (name: string, tick: any, index: number) => string;
    offset?: number; // 
    offsetX?: number; //  offset  x 
    offsetY?: number; //  offset  y 
    rotate?: number; // label 
    useHtml?: boolean; //  HTML 
    htmlTemplate?: string; //  label  html  useHtml: true 
    style?: IStyleConfig;
    autoRotate?: boolean;
    autoHide?: boolean;
  };
  title?: {
    visible?: boolean;
    autoRotate?: boolean;
    text?: string;
    offset?: number;
    style?: IStyleConfig;
  };
  tickLine?: {
    visible?: boolean;
    style?: IStyleConfig;
  };
  events?: IEvents;
}
/** Linear */
export interface IValueAxis extends IBaseAxis {
  type?: 'linear';
  /** tick */
  nice?: boolean;
  min?: number;
  max?: number;
  minLimit?: number;
  maxLimit?: number;
  tickCount?: number;
  tickInterval?: number;
}
/**  */
export interface ITimeAxis extends IBaseAxis {
  type?: 'time';
  /** tick */
  tickInterval?: string;
  tickCount?: number;
  groupBy?: string;
  mask?: string;
}
/**  */
export interface ICatAxis extends IBaseAxis {
  type?: 'cat';
  /** tick */
  tickInterval?: number;
  tickCount?: number;
  groupBy?: string;
}

export type Axis = ICatAxis | IValueAxis | ITimeAxis;

export interface Label {
  visible?: boolean;
  type?: string;
  formatter?: (text: string, item: any, idx: number, ...extras: any[]) => string;
  /**  label  */
  precision?: number;
  /**  */
  suffix?: string;
  style?: any;
  offset?: number;
  offsetX?: number;
  offsetY?: number;
  events?: IEvents;
  position?: string;
  adjustColor?: boolean;
  adjustPosition?: boolean;
  autoRotate?: boolean;
  labelLine?: any;
}

export interface Legend {
  visible?: boolean;
  /**  */
  position?: string;
  /**  */
  flipPage?: boolean;
  events?: IEvents;
  formatter?: (...args: any) => string;
  offsetX?: number;
  offsetY?: number;
  clickable?: boolean;
  title?: {
    visible?: boolean;
    spacing?: number;
    style?: IStyleConfig;
  };
}

export interface Tooltip {
  visible?: boolean;
  shared?: boolean;
  /** html */
  showTitle?: boolean;
  html?: HTMLDivElement;
  formatter?: (...args: any) => string;
  // htmlContent?: (title: string, items: any[]) => string;
  containerTpl?: string;
  itemTpl?: string;
  /**  */
  //crosshair?: 'x' | 'y' | 'cross' | boolean;
  //crosshairs?: { type: string; style?: IStyleConfig }; // FIXME:
  showCrosshairs?: boolean;
  crosshairs?: object;
  style?: IStyleConfig;
  offset?: number;
  showMarkers?: boolean;
}

interface Animation {
  /** or */
  type?: string;
  duration?: number;
  easing?: string;
}

// tslint:disable-next-line: no-empty-interface
export interface Theme {}

export interface ElementOption {
  type: string;
  position: {
    fields: string[];
  };
  color?: AttributeOption;
  size?: AttributeOption;
  shape?: AttributeOption;
  style?: IStyleConfig;
  label?: LabelOption | false;
  animate?: {};
  adjust?: AdjustOption[];
  connectNulls?: boolean;
  widthRatio?: {
    [type: string]: number;
  };
  tooltip?: any;
}

export type G2Config = Options;

export interface IColorConfig {
  fields?: string[];
  values?: string[];
  callback?: (...args: any[]) => any;
}

const SECOND = 1000;
const MINUTE = 60 * SECOND;
const HOUR = 60 * MINUTE;
const DAY = 24 * HOUR;
const WEEK = DAY * 7;
const MONTH = DAY * 30;
const YEAR = DAY * 365;
export const timeIntervals = {
  second: { value: SECOND, format: 'HH:mm:ss' },
  miniute: { value: MINUTE, format: 'HH:mm' },
  hour: { value: HOUR, format: 'HH' },
  day: { value: DAY, format: 'YYYY-MM-DD' },
  week: { value: WEEK, format: 'YYYY-MM-DD' },
  month: { value: MONTH, format: 'YYYY-MM' },
  year: { value: YEAR, format: 'YYYY' },
};

interface StateCondition {
  name: string;
  exp: () => boolean | string | number;
}

export interface StateConfig {
  condition: () => any | StateCondition;
  style?: IStyleConfig;
  related?: string[];
}

export interface ISliderInteractionConfig {
  /**  horizontal */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /** , 0~1 */
  start?: number;
  /** , 0~1 */
  end?: number;
  /**  */
  backgroundStyle?: ShapeAttrs;
  /**  */
  foregroundStyle?: ShapeAttrs;
  /**  */
  handlerStyle?: ShapeAttrs;
  /**  */
  textStyle?: ShapeAttrs;
  /** slider   */
  trendCfg?: {
    /**  smooth  */
    smooth?: boolean;
    /**  */
    isArea?: boolean;
    /**  */
    backgroundStyle?: ShapeAttrs;
    /**  */
    lineStyle?: ShapeAttrs;
    /**  */
    areaStyle?: ShapeAttrs;
  };
}

export interface IScrollbarInteractionConfig {
  /**  horizontal */
  type?: 'horizontal' | 'vertical';
  /**  vertical  */
  width?: number;
  /**  horizontal  */
  height?: number;
  /**  padding */
  padding?: [number, number, number, number];
  /** XX */
  categorySize?: number;
}

//export type IInteractionConfig = IScrollbarInteractionConfig | ISliderInteractionConfig;

export type IInteractionConfig = {
  [field: string]: any;
};

export interface IInteractions {
  type: string;
  cfg?: IInteractionConfig;
}

/**
 * 
 */
export interface Point {
  readonly x: number;
  readonly y: number;
}

export interface DataItem {
  [field: string]: string | number | number[] | null | undefined;
}

export interface IStyle {
  [field: string]: any;
}
